#ItemWallet {
  color: white !important;
  position: relative;
  width: 100%;
  height: 155px;
  /*height: 100%;*/
  /*width: calc(100% - 30px);*/
  max-width: 255px;
  /*height: calc(180px - 30px);*/
  border-radius: 6px;
  padding: 15px;
  display: grid;
  /*grid-template-rows: repeat(auto-fill, minmax(20px, 1fr)) !important;*/
  grid-template-rows: 35px 20px 25px 20px 1fr;
  transform: scale(1);
  transition: 0.2s;
  cursor: pointer;
  grid-row-gap: 3px;
  -webkit-box-shadow: 0px 0px 56px 3px rgba(0, 0, 0, 0.04);
  -moz-box-shadow: 0px 0px 56px 3px rgba(0, 0, 0, 0.04);
  box-shadow: 0px 0px 56px 3px rgba(0, 0, 0, 0.04);
  overflow: hidden;
  transform-origin: top;
  /*grid-area: wallet !important;*/
}

.retweetCont {
  display: grid;
