.ItemTemplate {
  /*width: 370px;*/
  /*height: 240px;*/
  border-radius: 6px;
  /*background: gray;*/
  position: relative;
  z-index: 2;
  width: 270px !important;
  transform: perspective(1000px) rotateX(-0deg) rotateY(0deg)
    scale3d(1.1, 1.1, 1.1);
  transition: 0.4s;
}

.ItemTemplate.animate {
  animation-name: logo;
  animation-duration: 7s;
  animation-iteration-count: infinite;
  animation-timing-function: ease;
}

@keyframes logo {
  0% {
    transform: perspective(1000px) rotateX(-14deg) rotateY(14deg)
      scale3d(1.1, 1.1, 1.1);
  }

  50% {
    transform: perspective(1000px) rotateX(14deg) rotateY(-14deg)
      scale3d(1.1, 1.1, 1.1);
  }

  100% {
    transform: perspective(1000px) rotateX(-14deg) rotateY(14deg)
      scale3d(1.1, 1.1, 1.1);
  }
}

